
const maxLottoNum1 = 33;
const maxLottoNum2 = 16;

export async function getAllTips(ticketNums) {
	
	var ticketsAsStr = "<b>Please add a few tips to your ticket.</b>";
	
	if(ticketNums.flat(1).length !== 0){					//arr is not empty, if there's at least one tip
		ticketsAsStr = "<table>";
		for(var i = 0; i < ticketNums.length; i++) {
			var ticketNum = ticketNums[i];
			if(typeof ticketNum[0] !== 'undefined'){
				ticketsAsStr += "<tr><th>#"+i+": </th>";
				for(var j = 0; j < ticketNum.length; j++) {
					ticketsAsStr += "<th>" + ticketNum[j] + "</th>";
				}
				ticketsAsStr += "</tr>";
			}
		}
		ticketsAsStr += "</table>";
	}
	return await ticketsAsStr;
}

export async function getAllWins(winsAsArray) {
	
	var winsAsStr = "<b>No luck yet!</b>";
	
	if(winsAsArray.flat(1).length !== 0){
		winsAsStr = "<table>";
		
		for(var i = 0; i < winsAsArray.length; i++) {
			if(i === 0){
				winsAsStr += "<tr><th>Matching numbers</th><th>Amount</th></tr>";
			}
			else{
				winsAsStr += "<tr><th>"+i+": </th><th>" + winsAsArray[i] + "</th></tr>";
			}
		}
		winsAsStr += "</table>";
	}
	return await winsAsStr;
}

export async function createTip(ticketNums, yourNums) {
	
	var firstArr = [parseInt(yourNums.one), parseInt(yourNums.two), parseInt(yourNums.three), parseInt(yourNums.four), parseInt(yourNums.five), parseInt(yourNums.six)];
	var lastArr  = [parseInt(yourNums.seven)];
	
	//check if numbers are too small or too large
	for(var i = 0; i < firstArr.length; i++) {
		if(!firstArr[i] || firstArr[i] < 1 || firstArr[i] > maxLottoNum1){
			alert("Invalid numbers detected!");
			return await [ticketNums, false];					// send back with no change
		}
	}
	if(!lastArr[0] || lastArr[0] < 1 || lastArr[0] > maxLottoNum2){
		alert("Invalid number detected!");
		return await [ticketNums, false];
	}
	
	var ticketChanged = false;
	firstArr.sort(function(a, b){return a-b});
	
	//same number in tip 
	if(hasDuplicates(firstArr)){
		alert("Identical numbers detected!");
	}
	var tipArr = firstArr.concat(lastArr);
	
	//same tip on ticket 
	if(arrayAlreadyHasArray(ticketNums, tipArr)){
		alert("Identical tip detected!");
	}
	//valid tip, add to ticket
	else{
		ticketChanged = true;
		ticketNums.push(tipArr);
	}
	
	return await [ticketNums, ticketChanged];
}

export async function randomTip(ticketNums) {
    
	var firstArr = [0,0,0,0,0,0];
	var helpFirstArr = [0,0,0,0,0,0];
	var lastArr = [0];
	
	while(onlyUniques(firstArr) || (arrayAlreadyHasArray(ticketNums, firstArr))){	// only get unique numbers in one tip  ||  no identical tips
		firstArr = helpFirstArr.map((element, index, array) => {
		  return Math.floor(Math.random() * maxLottoNum1) + 1;						// returns a random integer from 1 to 'maxLottoNum' (e.g. 45) 
		})
		firstArr.sort(function(a, b){return a-b});									// sort ascending order
	}
	lastArr[0] = Math.floor(Math.random() * maxLottoNum2) + 1;
	
	var tipArr = firstArr.concat(lastArr);
	ticketNums.push(tipArr);
	
	return await ticketNums;
}

function hasDuplicates(array) {
    return (new Set(array)).size !== array.length;
}

function onlyUniques(array) {
	return array.some((val, i) => array.indexOf(val) !== i);
}

function arrayAlreadyHasArray(arr, subarr){
	
    for(var i = 0; i < arr.length; i++){
        let checker = false;
		
        for(var j = 0; j < arr[i].length; j++){
			
            if(arr[i][j] === subarr[j]){
                checker = true;
            } else {
                checker = false;
                break;
            }
        }
        if (checker){
            return true;
        }
    }
    return false;
}
  
export async function startCalc(currDate, ticketNums, significantWinText, winsAsArray) {
	
	var firstJpArr = [0,0,0,0,0,0];
	var helpFirstJpArr = [0,0,0,0,0,0];
	var lastJpArr = [0];
	var wonLottery = false;
	
	currDate = increaseDate(currDate);
	const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
	var currDateStr = currDate.toLocaleDateString(undefined, options);
	
	// create winning lotto nums
	while(onlyUniques(firstJpArr)){										// only get unique numbers
		firstJpArr = helpFirstJpArr.map((element, index, array) => {
		  return Math.floor(Math.random() * maxLottoNum1) + 1;			// returns a random integer from 1 to 'maxLottoNum'
		})
	}
	lastJpArr[0] = Math.floor(Math.random() * maxLottoNum2) + 1;
	firstJpArr.sort(function(a, b){return a-b});						// sort ascending order
	
	var jackpotArr = firstJpArr.concat(lastJpArr);
	
	//calculate how much the user did win (how many identical nums)
	var highestCurrentWin = 0;
	for(var i = 0; i < ticketNums.length; i++) {
		var ticketNum = ticketNums[i];
		var currentWins = 0;	
		if(ticketNum.length > 1){										
			// first 6 nums
			for(var j = 0; j < ticketNum.length-1; j++) {				// How many (identical) numbers you got in this drawing
				for(var k = 0; k < jackpotArr.length-1; k++) {
					if(ticketNum[j] === jackpotArr[k]){  				// wenn 1 zahl 1x passt
						currentWins++;
					}
				}
			}
			// last num (blue ball)
			if(ticketNum[6] === jackpotArr[6]){  						// wenn 1 zahl 1x passt
				currentWins++; 
			}
		}
		if(highestCurrentWin < currentWins){
			highestCurrentWin = currentWins;
		}
		winsAsArray[currentWins] += 1;
	}
	
	//take the highest win and output it to the user
	var snackbarTime = 3000;
	var jpText = "";
	if(highestCurrentWin === 7){
		wonLottery = true;
		snackbarTime = 24000;
		jpText = " Jackpot!";
	}
	if(highestCurrentWin > 2){ //ab 3
		var x = document.getElementById("snackbar");
		x.className = "show";
		setTimeout(function(){ x.className = x.className.replace("show", ""); }, snackbarTime);
		significantWinText = highestCurrentWin + " numbers!" + jpText;
	}
	
	await sleep(100);
	
    return await [jackpotArr, significantWinText, wonLottery, currDate, currDateStr, winsAsArray];
}

//Ziehungsdatum erhöhen - am: Tuesday, Thursday and Sunday
function increaseDate(currDate){
	var dayOfWeek = 100;
	while(dayOfWeek !== 1 && dayOfWeek !== 3 && dayOfWeek !== 6){				// if there's no lottery drawing on this day
		dayOfWeek = currDate.getDay();
		currDate.setDate(currDate.getDate() + 1);
	}
	return currDate;
}

function sleep(ms) {
	return new Promise(resolve => setTimeout(resolve, ms));
}
