import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Confetti from 'react-dom-confetti';
import { Header } from './components/Header'
import { DisplayBoard } from './components/DisplayBoard'
import CreateTip from './components/CreateTip'
import { getAllTips, createTip, startCalc, randomTip, getAllWins } from './services/LottoService'

class App extends Component {

  state = {
    yourNums: {},
	
	//nums on ticket:
	ticketNums: [[]],
    numberOfTips: 0,
	ticketsAsStr: "<b>Please add a few tips to your ticket.</b>",
	
	//winning numbers:
	lottoNum1: 1,
	lottoNum2: 2,
	lottoNum3: 3,
	lottoNum4: 4,
	lottoNum5: 5,
	lottoNum6: 6,
	lottoNum7: "B",
	drawingNumber: 0,
	significantWinText: " ",
	wonLottery: false,
	winsAsStr: "<b>No luck yet!</b>",
	winsAsArray: [0,0,0,0,0,0,0,0],
	
	currDate: new Date(),
	currDateStr: "today",
	
	costsSnapshot: 0.00,
	timeBegin: (new Date()).toLocaleDateString(undefined, { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }),
	timeSnapshot: "",
	gameStarted: false,
	costPerTicket: 2.00,
	
	completed: false
  }

  createTip = (e) => {
	if(this.state.gameStarted === false){
      createTip(this.state.ticketNums, this.state.yourNums)
        .then(response => {
			  if(response[1] === true){
				this.setState({	ticketNums: response[0].slice(),
								numberOfTips: this.state.numberOfTips + 1});
			  }
      });
	}
	else {
		alert("Modifying the ticket isn't allowed once the simulation has started.");
	}
  }
  
  randomTip = (e) => {
	if(this.state.gameStarted === false){
      randomTip(this.state.ticketNums)
        .then(response => {
		  
		  this.setState({	ticketNums: response.slice(),
							numberOfTips: this.state.numberOfTips + 1});
      });
	}
	else {
		alert("Modifying the ticket isn't allowed once the simulation has started.");
	}
  }

  deleteTicket = () => {
	if(this.state.gameStarted === false){
		
		this.setState({ticketNums: [[]], numberOfTips: 0});
	}
	else {
		alert("Modifying the ticket isn't allowed once the simulation has started.");
	}
  }
  
  getAllTips = () => {
    getAllTips(this.state.ticketNums)
      .then(ticketsAsStrResponse => {
		
		this.setState({ticketsAsStr: ticketsAsStrResponse});
		
		// Handle the ticket modal
		var modal = document.getElementById("ticketModal");
		var span = document.getElementById("closeTicket");
		modal.style.display = "block";
		span.onclick = function() {
		  modal.style.display = "none";
		}
		window.onclick = function(event) {
		  if (event.target === modal) {
			modal.style.display = "none";
		  }
		} 
      });
  }
  
  getAllWins = () => {
    getAllWins(this.state.winsAsArray)
      .then(winsAsStrResponse => {
		
		this.setState({	winsAsStr: winsAsStrResponse,
						// vars for popup window:
						costsSnapshot: ((this.state.drawingNumber) * (this.state.ticketNums.length-1) * this.state.costPerTicket).toFixed(2),
						timeSnapshot: this.state.currDateStr});
		
		// Handle the wins modal
		var modal = document.getElementById("winsModal");
		var span = document.getElementById("closeWins");
		modal.style.display = "block";
		span.onclick = function() {
		  modal.style.display = "none";
		}
		window.onclick = function(event) {
		  if (event.target === modal) {
			modal.style.display = "none";
		  }
		} 
      });
  }

  startCalcInit = () => {
	  if(!this.state.gameStarted){
		this.startCalc(this.state.currDate, this.state.ticketNums, this.state.significantWinText, this.state.winsAsArray);
	  }
	  else {
		alert("The simulation has already started!");
	  }
  }

  startCalc = () => {
	startCalc(this.state.currDate, this.state.ticketNums, this.state.significantWinText, this.state.winsAsArray)
	  .then(lottoCalcVals => {
		  
		  if(this.state.numberOfTips > 0){
		  
			this.setState({	gameStarted: true,
							drawingNumber: this.state.drawingNumber + 1, 
							significantWinText: lottoCalcVals[1],
							lottoNum1: lottoCalcVals[0][0], 
							lottoNum2: lottoCalcVals[0][1], 
							lottoNum3: lottoCalcVals[0][2], 
							lottoNum4: lottoCalcVals[0][3], 
							lottoNum5: lottoCalcVals[0][4], 
							lottoNum6: lottoCalcVals[0][5],
							lottoNum7: lottoCalcVals[0][6],
							currDate: lottoCalcVals[3], 
							currDateStr: lottoCalcVals[4], 
							winsAsArray: lottoCalcVals[5],
							wonLottery: lottoCalcVals[2]});
			
			if(this.state.wonLottery === false){ // no Jackpot, continue...
				this.startCalc(this.state.currDate, this.state.ticketNums, this.state.significantWinText, this.state.winsAsArray, false);
			}
			else{ // Jackpot
				// throw confetti
				this.setState({	completed: true});
				this.setState({wonLottery: false});
				// victory sound
				var fw = new Audio("./sound/firework_explosion_fizz_002.mp3");
				fw.play();
				var vic = new Audio("./sound/victory.mp3");
				vic.play();
			}
		  }
		  else {
			  alert("Please get a ticket first.");
		  }
	  });
  }
  
  resetGame = () => {
	  this.setState({	ticketNums: [[]], 
						numberOfTips: 0, 
						ticketsAsStr: "<b>Please add a few tips to your ticket.</b>", 
						lottoNum1: 1, 
						lottoNum2: 2, 
						lottoNum3: 3, 
						lottoNum4: 4, 
						lottoNum5: 5, 
						lottoNum6: 6, 
						lottoNum7: "B",
						drawingNumber: 0, 
						significantWinText: " ", 
						wonLottery: false, 
						winsAsStr: "<b>No luck yet!</b>", 
						winsAsArray: [0,0,0,0,0,0,0,0], 
						currDate: new Date(), 
						currDateStr: "today", 
						costsSnapshot: 0.00, 
						timeBegin: (new Date()).toLocaleDateString(undefined, { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }), 
						timeSnapshot: "", 
						gameStarted: false,
						completed: false});
  } 

  onChangeForm = (e) => {
      let yourNums = this.state.yourNums
      if (e.target.name === 'one') {
          yourNums.one = e.target.value;
      } else if (e.target.name === 'two') {
          yourNums.two = e.target.value;
      } else if (e.target.name === 'three') {
          yourNums.three = e.target.value;
      } else if (e.target.name === 'four') {
          yourNums.four = e.target.value;
      } else if (e.target.name === 'five') {
          yourNums.five = e.target.value;
      } else if (e.target.name === 'six') {
          yourNums.six = e.target.value;
      } else if (e.target.name === 'seven') {
          yourNums.seven = e.target.value;
      }
      this.setState({yourNums})
  }

  render() {
    
    return (
      <div className="App">
        <Header></Header>
        <div className="container mrgnbtm">
          <div className="row">
            <div className="col-md-8">
                <CreateTip 
                  ticket={this.state.yourNums}
                  onChangeForm={this.onChangeForm}
                  createTip={this.createTip}
                  deleteTicket={this.deleteTicket}
				  startCalc={this.startCalcInit}
				  resetGame={this.resetGame}
				  randomTip={this.randomTip}
                  >
                </CreateTip>
            </div>
            <div className="col-md-4">
                <DisplayBoard
                  numberOfTips={this.state.numberOfTips}
                  getAllTips={this.getAllTips}
                  getAllWins={this.getAllWins}
                >
                </DisplayBoard>
            </div>
          </div>
          <div className="row mt-5">
			  <div className="col-md-6">
				<h2>Drawing number: {this.state.drawingNumber}</h2>
			  </div>
			  <div className="col-md-3 text-md-center text-right">
				Costs: ¥ { ((this.state.drawingNumber) * (this.state.ticketNums.length-1) * this.state.costPerTicket).toFixed(2) },-
			  </div>
			  <div className="col-md-3 text-right">
				{this.state.currDateStr}
			  </div>
		  </div>
        <div className="row mt-4 mb-5">
			<div className="container mb-5">
				<div className="d-flex justify-content-around">
					<Confetti active={ this.state.completed } />
					<button type="button" class="btn btn-default btn-circle btn-xl"><span class="shadow"></span>{this.state.lottoNum1}</button>
					<Confetti active={ this.state.completed } />
					<button type="button" class="btn btn-default btn-circle btn-xl"><span class="shadow"></span>{this.state.lottoNum2}</button>
					<Confetti active={ this.state.completed } />
					<button type="button" class="btn btn-default btn-circle btn-xl"><span class="shadow"></span>{this.state.lottoNum3}</button>
					<Confetti active={ this.state.completed } />
					<button type="button" class="btn btn-default btn-circle btn-xl"><span class="shadow"></span>{this.state.lottoNum4}</button>
					<Confetti active={ this.state.completed } />
					<button type="button" class="btn btn-default btn-circle btn-xl"><span class="shadow"></span>{this.state.lottoNum5}</button>
					<Confetti active={ this.state.completed } />
					<button type="button" class="btn btn-default btn-circle btn-xl"><span class="shadow"></span>{this.state.lottoNum6}</button>
					<Confetti active={ this.state.completed } />
					<button type="button" class="btn btn-default btn-circle btn-xl btn-blue"><span class="shadow"></span>{this.state.lottoNum7}</button>
					<Confetti active={ this.state.completed } />
				</div>
			</div>
		  </div>
        </div>
		<div id="snackbar">{this.state.significantWinText}</div>
		<div id="ticketModal" class="modal">
		  <div class="modal-content">
			  <div class="modal-header">
				<h2>Lottery Ticket</h2>
				<span class="close" id="closeTicket">&times;</span>
			  </div>
			  <div class="modal-body">
				<div dangerouslySetInnerHTML={{ __html: this.state.ticketsAsStr }} />
				<p>This is your lottery ticket. It does cost {((this.state.ticketNums.length-1) * this.state.costPerTicket).toFixed(2)} yuan. Take good care of it and don't lose it. Good luck!</p>
				<p><b>Disclaimer:</b> This program does only simulate a lottery game. This website is not affiliated with any lottery game company. It is merely a simulation and no real money is used in it.</p>
			  </div>
			</div>
		</div>
		<div id="winsModal" class="modal">
		  <div class="modal-content">
			  <div class="modal-header">
				<h2>Wins</h2>
				<span class="close" id="closeWins">&times;</span>
			  </div>
			  <div class="modal-body">
				<div dangerouslySetInnerHTML={{ __html: this.state.winsAsStr }} />
				<p>All winnings achieved so far (from {this.state.timeBegin} to {this.state.timeSnapshot}) are listed here. You have spent ¥ { this.state.costsSnapshot },- so far.</p>
				<p><b>Disclaimer:</b> This program does only simulate a lottery game. This website is not affiliated with any lottery game company. It is merely a simulation and no real money is used in it.</p>
			  </div>
			</div>
		</div>
      </div>
    );
  }
}

export default App;
